
"use client";

import { useState } from 'react';
import { useAuth } from '@/context/AuthContext';
import ProtectedRoute from '@/components/ProtectedRoute';
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Progress } from "@/components/ui/progress";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { CheckCircle2, AlertTriangle, BookOpen, Clock } from "lucide-react";

// Mock Data for UI Demonstration
const courses = [
    { code: "CS301", name: "Data Structures & Algorithms", credits: 4, type: "Major", attendance: 85, schedule: "Mon, Wed 10:00 AM" },
    { code: "CS302", name: "Database Management Systems", credits: 4, type: "Major", attendance: 72, schedule: "Tue, Thu 11:30 AM" },
    { code: "MA201", name: "Linear Algebra", credits: 3, type: "Minor", attendance: 90, schedule: "Fri 09:00 AM" },
    { code: "HU101", name: "Effective Communication", credits: 2, type: "Elective", attendance: 65, schedule: "Mon 02:00 PM" },
];

export default function CourseManagerPage() {
    return (
        <ProtectedRoute allowedRoles={['student']}>
            <div className="max-w-6xl mx-auto py-10 space-y-8 animate-in fade-in-50">
                <div className="flex justify-between items-start">
                    <div>
                        <h1 className="text-3xl font-bold tracking-tight">Academic Mastery</h1>
                        <p className="text-muted-foreground">Track your academic progress, attendance, and course details.</p>
                    </div>
                    <div className="flex gap-4">
                        <Card className="bg-primary text-primary-foreground p-4 w-32 text-center">
                            <h3 className="text-2xl font-bold">13</h3>
                            <p className="text-xs opacity-80">Total Credits</p>
                        </Card>
                        <Card className="bg-secondary text-secondary-foreground p-4 w-32 text-center">
                            <h3 className="text-2xl font-bold">78%</h3>
                            <p className="text-xs opacity-80">Avg Attendance</p>
                        </Card>
                    </div>
                </div>

                {/* Course List & Attendance */}
                <Card>
                    <CardHeader>
                        <CardTitle className="flex items-center gap-2"><BookOpen className="w-5 h-5" /> Enrolled Courses</CardTitle>
                    </CardHeader>
                    <CardContent>
                        <Table>
                            <TableHeader>
                                <TableRow>
                                    <TableHead>Code</TableHead>
                                    <TableHead>Course Name</TableHead>
                                    <TableHead>Type</TableHead>
                                    <TableHead>Schedule</TableHead>
                                    <TableHead className="w-[200px]">Attendance</TableHead>
                                </TableRow>
                            </TableHeader>
                            <TableBody>
                                {courses.map((course) => (
                                    <TableRow key={course.code}>
                                        <TableCell className="font-medium">{course.code}</TableCell>
                                        <TableCell>{course.name}</TableCell>
                                        <TableCell><Badge variant="outline">{course.type}</Badge></TableCell>
                                        <TableCell className="text-muted-foreground text-sm"><Clock className="w-3 h-3 inline mr-1" />{course.schedule}</TableCell>
                                        <TableCell>
                                            <div className="space-y-1">
                                                <div className="flex justify-between text-xs">
                                                    <span className={course.attendance < 75 ? "text-destructive font-bold" : "text-green-600 font-bold"}>
                                                        {course.attendance}%
                                                    </span>
                                                    <span className="text-muted-foreground">Target: 75%</span>
                                                </div>
                                                <Progress value={course.attendance} className={`h-2 ${course.attendance < 75 ? "bg-red-100" : ""}`} color={course.attendance < 75 ? "bg-red-500" : "bg-green-500"} />
                                                {course.attendance < 75 && (
                                                    <div className="flex items-center text-xs text-destructive mt-1">
                                                        <AlertTriangle className="w-3 h-3 mr-1" /> Low Attendance Warning
                                                    </div>
                                                )}
                                            </div>
                                        </TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </CardContent>
                </Card>

                {/* Academic Calendar Widget Placeholder */}
                <div className="grid md:grid-cols-2 gap-6">
                    <Card>
                        <CardHeader>
                            <CardTitle>Upcoming Exams</CardTitle>
                        </CardHeader>
                        <CardContent>
                            <div className="space-y-4">
                                <div className="flex justify-between items-center border-b pb-2">
                                    <div className="flex flex-col">
                                        <span className="font-semibold">DBMS Mid-Sem</span>
                                        <span className="text-xs text-muted-foreground">March 15, 2026</span>
                                    </div>
                                    <Badge>Upcoming</Badge>
                                </div>
                                <div className="flex justify-between items-center border-b pb-2">
                                    <div className="flex flex-col">
                                        <span className="font-semibold">DSA Lab Eval</span>
                                        <span className="text-xs text-muted-foreground">March 18, 2026</span>
                                    </div>
                                    <Badge variant="secondary">Lab</Badge>
                                </div>
                            </div>
                        </CardContent>
                    </Card>

                    <Card>
                        <CardHeader>
                            <CardTitle>Notifications</CardTitle>
                        </CardHeader>
                        <CardContent>
                            <div className="space-y-2 text-sm text-muted-foreground">
                                <p>• Registration for Electives starts on Feb 20th.</p>
                                <p>• Library dues must be cleared before exams.</p>
                                <p>• Hostel fees deadline extended to March 1st.</p>
                            </div>
                        </CardContent>
                    </Card>
                </div>
            </div>
        </ProtectedRoute>
    );
}
